// @ts-nocheck
import React, { useEffect, useState, useContext } from 'react';
import { Card, Button } from "@material-ui/core";
import { FirebaseContext } from "../firebaseContext";
import loading from '../assets/loading2.gif';

export const ProfileList = () => {
  const [data, setData] = useState();
  const { getAllProfiles, getObjkts } = useContext(FirebaseContext);
  const [loadingWallets, setLoadingWallets] = useState(true);

  useEffect(() => {
    async function grab() {
      let res = await getAllProfiles();
      console.log(res);

      const profilePreview = await Promise.all(res.map(async ({ address }) => {
        let objkts = await getObjkts(address);
        let randomBG;
        let created = 0;
        let owned = 0;
        if (objkts) {
          created = objkts.created.token.length;
          owned = objkts.owned.token_holder.length;

          console.log(objkts.owned.token_holder)
          let randomIndex = Math.floor(Math.random() * objkts.owned.token_holder.length);
          if ((objkts.owned.token_holder[randomIndex]) && ((objkts.owned.token_holder[randomIndex]).token.display_uri))
            randomBG = ((objkts.owned.token_holder[randomIndex]).token.display_uri).replace('ipfs://', 'https://ipfs.io/ipfs/');
          else {
            console.log(objkts.owned.token_holder)
            console.log(randomIndex + " " + objkts.owned.token_holder.length)
          }
        }
        return { wallet: address, bg: randomBG, created: created, owned: owned }
      }));
      console.log(await profilePreview)
      setData(await profilePreview);
      setLoadingWallets(false);
    }
    grab();


  }, [getAllProfiles, getObjkts]);

  return (
    <Card style={{ height: "100vh", overflow: "auto", backgroundColor: "black", fontFamily: "Neue-regular" }}>

      <Button
        size={"large"}
        title={"Return Back"}
        onClick={() => {
          window.open(`https://assemblr.xyz/`, "_blank")
        }}
        style={{ color: "white" }}
      >
        <u>Return Back</u>
      </Button>
      <br></br>
      <div>
        {loadingWallets && <img src={loading} alt={loading} height="100%" />}
      </div>
      {data && data.map((profile) => (
        profile &&
        <div style={{ display: "flex", justifyContent: "center" }}>
          <Card style={{ backgroundImage: `url(${profile.bg})`, width: "100%", textAlign: "center", height: "20vh" }}>
            <Button
              size={"small"}
              title={profile.wallet}
              onClick={() => {
                window.open(`https://assemblr.xyz/?profile=${profile.wallet}`, "_blank")
              }}
              style={{ backgroundColor: "black", width: "100%", color: "white" }}
            >
              <u>{profile.wallet}</u>&nbsp;

              created: {profile.created}/100&nbsp;
              owned: {profile.owned}/100
            </Button>
          </Card>
        </div>
      ))
      }


    </Card>
  );
}
