// @ts-nocheck
import "./App.css";
import React, { useEffect, useState } from "react";

//ui
import { Button, TextField, Card, IconButton } from "@material-ui/core";

//logic
import { DAppClient } from "@airgap/beacon-sdk";
//import _ from "underscore";
//import { FirebaseContext } from "./firebaseContext";
import { Profile } from './components/profile';
import { useNavigate } from "react-router-dom";
import { ProfileList } from "./components/ProfileList";

import discord from "./assets/discord-192.png";

const dAppClient = new DAppClient({ name: "Beacon Docs" });


function App() {
  const navigate = useNavigate();
  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState("sync");
  const [showUnsync, setShowUnsync] = useState(false);
  const [address, setAddress] = useState();
  const [showList, setShowList] = useState(false);

  useEffect(() => {
    async function getAddress() {
      //console.log(new URL(window.location.href).searchParams.get('profile'))
      let profileAddress = new URL(window.location.href).searchParams.get('profile');
      let list = new URL(window.location.href).pathname;
      //console.log(list)
      //setAddress(profileAddress);
      if (list === "/directory") {
        setShowList(true);
      }
      else {
        if (!(profileAddress && profileAddress.includes(".tez"))) {
          setAddress(profileAddress);
        }
        else if (profileAddress) {

          await fetch('https://api.tezos.domains/graphql', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              query: `
                {
                  domain(name: "` + profileAddress + `") {
                    name
                    address
                  }
                }
                `,
              variables: {
              },
            }
            ),
          })
            .then((res) => res.json())
            .then((result) => {
              //console.log(result);
              if (result.data.domain) {
                setAddress(result.data.domain.address);
              }
            });
        }
      }
    }
    getAddress();
  }, [setAddress]);

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(
          activeAccount.address.slice(0, 6) +
          "..." +
          activeAccount.address.slice(32, 36)
        );
        setShowUnsync(true);
        if (!address && !showList) {
          navigate("/?profile=" + activeAccount.address);
        }
      } else {
        setSynced("sync");
        setShowUnsync(false);
      }
    }
    getAcc();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeAccount]);

  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount());
        setSynced("sync");
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address);
      setShowUnsync(true);
      if (!address) {
        navigate("/?profile=" + activeAccount.address);
      }
      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount());
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address);
        setShowUnsync(true);
        navigate("/?profile=" + permissions.address);
      } catch (error) {
        console.log("Got error:", error);
      }
    }
  }

  return (
    <div

    >
      <div
        className="top-left"
        style={{
          fontSize: "1.5em",
          display: "flex",
          alignItems: "center",
          margin: 6,
          fontFamily: "Neue-bold",
          marginRight: 22,
        }}
      >
        <b>Profile Assemblr </b>
      </div>



      <div style={{ position: "absolute", top: 4, left: "auto", right: "auto", fontFamily: "Neue-regular", width: "100%" }}>

        <div style={{ textAlign: "center", fontSize: "1.1em", fontFamily: "Neue-regular" }}>
          {address ?
            <div>
              Auto Generated Profile for Tezos Wallet Address {address}
            </div> :
            <div>
              Auto Generated Profile for Tezos
            </div>
          }
        </div>

      </div>

      {
        (address || activeAccount) && !showList &&
        <Profile
          key={activeAccount + address}
          address={address ? address : activeAccount.address}
          account={activeAccount}
          setAddress={setAddress}
          navigate={navigate}
        />
      }
      {showList && <ProfileList />}
      <div
        className="top-right"
        style={{ position: "absolute", display: "flex", alignItems: "center" }}
      >
        {showUnsync && (
          <Button
            size={"small"}
            title={"unsync"}
            onClick={() => {
              unsync();
            }}
          >
            <u>unsync</u>{" "}
          </Button>
        )}

        {showUnsync && <div> | </div>}
        <Button
          title={"sync"}
          size={"small"}
          onClick={async () => {
            if (!showUnsync) {
              await sync();
            } else {
              navigate("/?profile=" + activeAccount.address);
              setAddress(activeAccount.address);
            }
          }}
        >
          <u>{synced}</u>{" "}
        </Button>
      </div>

      <div
        className="bottom"
        style={{ position: "sticky" }}
      >




        <div
          style={{ position: "sticky", display: "flex", alignItems: "center", justifyContent: "space-between", backgroundColor: "white", width: "100%", overflow: "scroll" }}
        >
          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurenetworks.net/#/", "_blank");
            }}

          >
            <u>Adventure Network</u>{" "}
          </Button>
          <IconButton
            onClick={() => { window.open("https://discord.gg/eHemqMfFAV") }}
          >
            <img src={discord} alt="discord" width="20" height="20"></img>
          </IconButton>

          <Button
            style={{ marginInline: 2, minWidth: 100 }}
            size={"small"}
            onClick={async () => {
              //window.open("https://assemblr.xyz/directory", "_blank");
              navigate("/discovery");
            }}

          >
            <u>DISCOVERY</u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 100 }}
            size={"small"}
            onClick={async () => {
              //window.open("https://assemblr.xyz/directory", "_blank");
              navigate("/directory");
            }}

          >
            <u>Directory</u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://trydrum.net/", "_blank");
            }}
          >
            <u> TryDrum</u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://peopleparty.xyz/", "_blank");
            }}
          >
            <u> People Party </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://elementals.run/", "_blank");
            }}
          >
            <u> Elementals </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://network1.cc/", "_blank");
            }}
          >
            <u> Network 1 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network2/", "_blank");
            }}
          >
            <u> Network 2 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network3/", "_blank");
            }}
          >
            <u> Network 3 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network4/", "_blank");
            }}
          >
            <u> Network 4 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://asknetwork.xyz/", "_blank");
            }}
          >
            <u> Network 5 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://soundsofpi.com/", "_blank");
            }}
          >
            <u> Sounds of Pi </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://www.supmail.net/", "_blank");
            }}
          >
            <u> Sup Mail </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://metaverses.fyi/#/", "_blank");
            }}
          >
            <u> Metaverses.fyi </u>{" "}
          </Button>
        </div>
      </div>
    </div >
  );
}

export default App;
